import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, TextField } from '@mui/material';
import React, { useCallback, useState } from 'react';
import { useDispatch } from 'react-redux';
import { MessagesEffects } from '../../../store/slices/MessagesSlice/MessagesSlice';
import { toast } from 'react-toastify';

const WriteResponseDialog = ({dialogOpen, setDialogOpen, messageId}) => {
	const dispatch = useDispatch();
	const [message, setMessage] = useState('');

	const sendMessage = useCallback((currentMessage) => {
		try {
			dispatch(MessagesEffects.sendMessage({message: currentMessage, messageId}))
				.unwrap()
				.then(() => {
					toast.success('  ');
					setDialogOpen(false);
				});
		} catch {
			toast.error(' .   ');
		}
	}, [messageId, dispatch, setDialogOpen]);

  	return (
  	    <Dialog open={dialogOpen} onClose={() => setDialogOpen(false)}>
  	        <DialogTitle>  </DialogTitle>
  	        <DialogContent>
  	          <DialogContentText>
  	                ,       .   ,   ,  "",    ""
  	          </DialogContentText>
  	          <TextField
  	            autoComplete="new-password"
  	            autoFocus
  	            required
  	            margin="dense"
  	            label=" "
  	            type="text"
  	            fullWidth
  	            variant="standard"
				value={message}
				onChange={(e) => setMessage(e.target.value)}
  	          />
  	        </DialogContent>
  	        <DialogActions>
  	          <Button onClick={() => setDialogOpen(false)}></Button>
  	          <Button type="submit" onClick={() => sendMessage(message, messageId)}></Button>
  	        </DialogActions>
  	    </Dialog>
  	);
};

export default WriteResponseDialog;
